<template>
  <v-container>
    <h1>{{ hour }}:{{ min }}:{{ sec }}</h1>
    <h3>{{ date }}</h3>

    <br />

    <div class="alert alert-primary" role="alert" v-if="notice.length > 0">
      {{ notice }}
    </div>

    <div class="card">
      <div class="card-header">급식 (한솔고등학교 - {{ updatedDate }})</div>
      <div class="card-body">
        <div class="row">
          <div class="col-sm-4">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">[조식]</h5>
                <p class="card-text" v-if="breakfast.length == 0">
                  등록된 정보가 없습니다
                </p>
                <p
                  class="card-text"
                  v-else
                  v-html="breakfast.join('<br />')"
                ></p>
              </div>
            </div>
          </div>
          <div class="col-sm-4">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">[중식]</h5>
                <p class="card-text" v-if="lunch.length == 0">
                  등록된 정보가 없습니다
                </p>
                <p class="card-text" v-else v-html="lunch.join('<br />')"></p>
              </div>
            </div>
          </div>
          <div class="col-sm-4">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">[석식]</h5>
                <p class="card-text" v-if="dinner.length == 0">
                  등록된 정보가 없습니다
                </p>
                <p class="card-text" v-else v-html="dinner.join('<br />')"></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <br />
    <h2>한솔고등학교</h2>
  </v-container>
</template>

<script>
const axios = require("axios");

var dotw = ["일", "월", "화", "수", "목", "금", "토"];

export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  data: () => {
    return {
      hour: "",
      min: "",
      sec: "",
      date: "",
      updatedDate: "",
      breakfast: [],
      lunch: [],
      dinner: [],
      notice: "",
    };
  },
  methods: {
    get_now_time() {
      var now_time = new Date(Date.now());
      this.hour = now_time.getHours().toString();
      this.min = now_time.getMinutes().toString();
      this.min = (this.min.length == 1 ? "0" : "") + this.min;
      this.sec = now_time.getSeconds().toString();
      this.sec = (this.sec.length == 1 ? "0" : "") + this.sec;
      this.date =
        now_time.getFullYear() +
        "/" +
        (now_time.getMonth() + 1) +
        "/" +
        now_time.getDate() +
        "(" +
        dotw[now_time.getDay()] +
        ")";
      setTimeout(this.get_now_time, 100);
    },
  },
  mounted() {},
  created() {
    setTimeout(this.get_now_time, 100);
    axios
      .get(
        "https://raw.githubusercontent.com/hshanlee/data_test/main/kiosk/menu.json"
      )
      .then((response) => {
        // handle success
        console.log(response);
        var now_time = new Date(Date.now());
        var datemark =
          now_time.getFullYear().toString() +
          (now_time.getMonth() < 10 ? "0" : "") +
          (now_time.getMonth() + 1) +
          (now_time.getDate() < 10 ? "0" : "") +
          now_time.getDate();
        this.updatedDate = datemark;
        console.log(response.data[datemark].breakfast);
        this.breakfast = response.data[datemark].breakfast;
        this.lunch = response.data[datemark].lunch;
        this.dinner = response.data[datemark].dinner;
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      });
    axios
      .get(
        "https://raw.githubusercontent.com/hshanlee/data_test/main/kiosk/notice.json"
      )
      .then((response) => {
        this.notice = response.data.content;
        console.log(response);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      });
  },
};
</script>

